.booking {
  padding: 50px 163px;
  background-image: url("../img/reservation-desktop-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #daf1fd;

  @media (min-width: 1024px) {
    @include retina {
      background-image: url("../img/reservation-desktop-bg@2x.png");
    }
  }

  @include vp-1023 {
    display: block;
    padding: 100px;
    padding-top: 88px;
    background-image: url("../img/reservation-tablet-bg.png");

    @include retina {
      background-image: url("../img/reservation-tablet-bg@2x.png");
    }
  }

  @include vp-767 {
    padding: 65px 15px;
    padding-top: 59px;
    background-image: url("../img/reservation-mobile-bg.png");

    @include retina {
      background-image: url("../img/reservation-mobile-bg@2x.png");
    }
  }
}

.booking__container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  width: auto;
  margin: 0 auto;

  @include vp-1023 {
    display: block;
  }
}

.booking__inner {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding-top: 163px;
  margin-bottom: 42px;
  margin-right: 144px;

  @include vp-1023 {
    padding-top: 0;
    margin-right: 0;
  }

  @include vp-767 {
    margin-bottom: 24px;
  }
}

.booking__wrapper {
  width: 403px;

  @include vp-1023 {
    margin: 0 auto;
  }

  @include vp-767 {
    width: auto;
  }
}

.booking h2 {
  font-size: 40px;
  line-height: 60px;
  font-weight: 700;
  color: $color-tangaroa;
  margin: 0;
  margin-bottom: 8px;

  @include vp-1023 {
    font-size: 34px;
    line-height: 51px;
    text-align: center;
    margin-bottom: 5px;
  }

  @include vp-767 {
    font-size: 24px;
    line-height: 31px;
    margin-bottom: 14px;
  }
}

.booking p {
  font-size: 18px;
  line-height: 27px;
  color: $color-tangaroa;
  margin: 0;
  margin-bottom: 10px;

  @include vp-1023 {
    font-size: 18px;
    line-height: 27px;
    text-align: center;
  }

  @include vp-767 {
    font-size: 16px;
    line-height: 22px;
  }
}

.booking__indent {
  display: none;

  @include vp-767 {
    display: block;
  }
}

.booking__separator {
  display: block;

  @include vp-767 {
    display: none;
  }
}
